import React from "react";
import { Canvas } from "@react-three/fiber"; // Three.js React    
import { OrbitControls } from "@react-three/drei"; //       
import { Box } from "./ThreeCom/Box"; //    
import { Land } from "./ThreeCom/Land"; //    

const ThreeDimension = () => {
  return (
    <Canvas camera={{ position: [0, 1, 5] }}>
      {/* 3D    ,     */}
      <ambientLight intensity={1} />
      {/*   ()  */}
      <directionalLight position={[5, 0, 5]} intensity={3} />
      {/*      */}
      <Box />
      <Land />
      {/*    */}
      <OrbitControls />
      {/*  3D      */}
    </Canvas>
  );
};

export default ThreeDimension;
